.main {
  display: flex;
  height: 100%;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-color: var(--system-theme-background-color, #fff);
}

.textColor {
  color: var(--system-text-color, rgba(0, 0, 0, 0.85));
}

.avatarColor {
  color: #fff;
  background-color: var(--system-theme-color, rgba(0, 0, 0, 0.25));
}
