.container {
  width: 100%;
  height: 100%;
  user-select: none;
  transition: background 0.5s;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  text-transform: uppercase;
  position: relative;

  .panel {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    max-width: 650px;
    max-height: 430px;
    transition: 2s;
    transform: translateY(-15%);

    .wrapper {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .pieces {
      width: 100%;
      height: 100%;
      transition-duration: .9s;
      transition-timing-function: cubic-bezier(.37, -.29, 0, 1.29);
    }

    .wrapper {
      z-index: 2;
    }

    .extra {
      z-index: 1;
    }

    .extra .pieces {
      clip-path: polygon(50% 50%, 50% 50%, 50% 50%);
    }

    .wrapper:nth-of-type(31) .pieces {
      transition-delay: .08s;
    }

    .wrapper:nth-of-type(32) .pieces {
      transition-delay: .16s;
    }

    .wrapper:nth-of-type(33) .pieces {
      transition-delay: .24s;
    }

