.vaquita .extra .pieces {
	background-color: hsla(0, 0%, 100%, 0);
	-webkit-animation: bubbles 2s infinite forwards;
	animation: bubbles 2s infinite forwards
}

.vaquita .wrapper:nth-child(31) .pieces {
	-webkit-clip-path: polygon(15% 18.5%, 15.5% 22%, 17% 20%);
	clip-path: polygon(15% 18.5%, 15.5% 22%, 17% 20%)
}

.vaquita .wrapper:nth-child(32) .pieces {
	-webkit-clip-path: polygon(11.67% 9.5%, 12.3% 8.4%, 12.7% 9.7%);
	clip-path: polygon(11.67% 9.5%, 12.3% 8.4%, 12.7% 9.7%);
	-webkit-animation-delay: .3s;
	animation-delay: .3s
}

.vaquita .wrapper:nth-child(33) .pieces {
	-webkit-clip-path: polygon(7.75% 35.5%, 9.4% 37%, 9.1% 34.5%);
	clip-path: polygon(7.75% 35.5%, 9.4% 37%, 9.1% 34.5%);
	-webkit-animation-delay: .6s;
	animation-delay: .6s
}

body.animation-lock.state-1.vaquita .wrapper:nth-child(16) .pieces {
	-webkit-clip-path: polygon(25.99% 61.716%, 32.64% 61.368%, 32.91% 73.146%);
	background-color: #0d1417
}

body.animation-lock.state-1.vaquita .wrapper:nth-child(18) .pieces {
	-webkit-clip-path: polygon(27.107% 55.964%, 39.318% 59.352%, 38.467% 66.772%);
	background-color: #334543
}

body.animation-lock.state-1.vaquita .wrapper:nth-child(19) .pieces {
	-webkit-clip-path: polygon(27.107% 55.964%, 38.467% 66.772%, 42.128% 75.085%);
	background-color: #21282f
}

.vaquita .shadow {
	left: 9%;
	width: 70%
}

.vaquita .shadow:after,
.vaquita .shadow:before {
	left: 20%;
	bottom: 22%;
	width: 30%;
	height: 50%
}

.vaquita .shadow:before {
	left: 97%
}

.vaquita {
	background-color: #64d6e2
}

.vaquita .popout {
	color: #64d6e2
}

.vaquita .nav .muted .mute span {
	border-color: #64d6e2
}

.vaquita .nav-btn:after {
	background-color: #64d6e2
}

.vaquita .animal-nav .why span {
