.container {
  width: 100%;
  height: 100%;
  user-select: none;
  .content {
    width: 100%;
    height: 100%;
    position: relative;
    .wallpaper {
      width: 100%;
      height: 100%;
      background-image: url(~@/assets/image/polygon-demo.png);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-position: left top;
      // opacity: 0.1;
      position: absolute;
      z-index: 0;
    }
    .pieces {
      width: 100%;
      height: 100%;
      background-color: #fff88b;
      transition: 0.3s;
      clip-path: polygon(0% 0%, 0% 0%, 0% 0%);
    }
    .dot {
      width: 1px;
      height: 1px;
      border-radius: 50%;
      background-color: #000;
      position: absolute;
      animation: breathing 2s ease infinite;
    }
    @keyframes breathing {
      0% {
        box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.9);
      }
      50% {
        box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.9);
      }
      100% {
        box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.9);
      }
    }
  }
}
